import { useContext } from 'react';
import { Alert } from 'reactstrap';
import {MyContext} from '../shared/myProvider.react';

function Results() {
  const {
    name,
    ttl,
    type,
  } = useContext(MyContext);
  return <div>
    <h2>Results</h2>
    <Alert color="primary">
      Your {type} works!
    </Alert>
    <Alert color="success">
      {name} is accessible!
    </Alert>
    <Alert color="danger">
      A TTL of {ttl} is too long!
    </Alert>
    <Alert color="warning">
      The data field is missing!
    </Alert>
  </div>;
}

export default Results;
