import { useContext } from 'react';
import { Col, Form, FormGroup, Input, Label, Row } from 'reactstrap';
import SelectType from './selectType.react';
import {MyContext} from '../shared/myProvider.react';

function InputForm() {
  const {
    data,
    name,
    ttl,
    type,
    setData,
    setName,
    setType,
    setTtl,
  } = useContext(MyContext);

  return <Form>
      <Row>
        <Col md={4}>
          <Label>Host Name</Label>
          <Input 
            onChange={e => setName(e.target.value)}
            type="text" 
            value={name} 
          />
        </Col>
        <Col md={2}>
          <FormGroup>
            <Label>Type</Label>
          <SelectType 
            value={type} 
            onChange={e => setType(e.target.value)}
          />
          </FormGroup>
        </Col>
        <Col md={2}>
          <FormGroup>
            <Label>TTL</Label>
            <Input
              type="number"
              value={ttl}
              onChange={e => setTtl(e.target.value)}
            />
          </FormGroup>
        </Col>
        <Col md={4}>
          <FormGroup>
            <Label>Data</Label>
            <Input 
              type="text" 
              value={data} 
              onChange={e => setData(e.target.value)} 
            />
          </FormGroup>
        </Col>
      </Row>
      </Form>;
}

export default InputForm;
